
.collapsible .toggle {
	background: white;
	border: unset;
	cursor: pointer;
	padding: 0.3rem;
	margin-bottom: 0.5rem;
}

.collapsible .content {
	padding: 0.5rem;
	margin-bottom: 0.5rem;
}

.collapsible .content-parent {
	height: 0px;
	overflow: hidden;
	transition: height ease 0.3s;
}